.about {
    height: 100%;
    width: 20%;
    margin-left: -100px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: black;
    overflow-x: hidden;
    transition: margin-left .2s linear;
    padding-top: 60px;
    color: white;
    height: 100%;
    font-family: Roboto;
    padding: 4%; 
}

.about:hover{
  margin-left: 0;
}

.about-close {
  color: white;
  font-size: 2em;
}

.about-header{
  display: flex;
  justify-content: space-between;
}

.about-title, .about-close{
  padding: 2%;
  font-size: 2em;
}

.about-description {
  margin-top: 15%;
  text-align: left;
  line-height: 1.5em;
  font-size: 1.5em;
}